import { Trans } from '@lingui/react';
import Rotation from 'components/ui/Rotation';
import React from 'react';
import { Button, Table } from 'semantic-ui-react';
import { formatDuration } from 'utilities';
export class RotationTable extends React.Component {
    render() {
        const { targets, notes, data, onGoto, headerTitle, } = this.props;
        return <Table compact unstackable celled>
			<Table.Header>
				<Table.Row>
					<Table.HeaderCell collapsing>
						<strong><Trans id="core.ui.rotation-table.header.time">Time</Trans></strong>
					</Table.HeaderCell>
					{(targets || []).map((target, i) => <Table.HeaderCell key={`target_header_${i}`} textAlign="center" collapsing>
								<strong>{target.header}</strong>
							</Table.HeaderCell>)}
					<Table.HeaderCell>
						<strong>{(headerTitle) ? headerTitle : <Trans id="core.ui.rotation-table.header.rotation">Rotation</Trans>}</strong>
					</Table.HeaderCell>
					{(notes || []).map((note, i) => <Table.HeaderCell key={`note_header_${i}`} textAlign="center" collapsing>
								<strong>{note.header}</strong>
							</Table.HeaderCell>)}
				</Table.Row>
			</Table.Header>
			<Table.Body>
				{data.map((entry) => <RotationTable.Row key={entry.start} onGoto={onGoto} targets={targets || []} notes={notes || []} {...entry}/>)}
			</Table.Body>
		</Table>;
    }
}
RotationTable.targetAccessorResolver = (entry, target) => {
    if (typeof target.accessor === 'string' && entry.targetsData != null) {
        return entry.targetsData[target.accessor];
    }
    else if (typeof target.accessor === 'function') {
        return target.accessor(entry);
    }
    else {
        return {
            actual: 0,
            expected: 0,
        };
    }
};
RotationTable.notesAccessorResolver = (entry, note) => {
    if (typeof note.accessor === 'string' && entry.notesMap != null) {
        return entry.notesMap[note.accessor];
    }
    else if (typeof note.accessor === 'function') {
        return note.accessor(entry);
    }
    else {
        return null;
    }
};
RotationTable.TargetCell = ({ actual, expected }) => <Table.Cell textAlign="center" positive={expected === undefined ? false : actual >= expected} negative={expected === undefined ? false : actual < expected}>
			{actual}/{expected === undefined ? '-' : expected}
		</Table.Cell>;
RotationTable.Row = ({ onGoto, targets, notes, notesMap, start, end, targetsData, rotation }) => <Table.Row>
			<Table.Cell textAlign="center">
				<span style={{ marginRight: 5 }}>{formatDuration(start / 1000)}</span>
				{typeof onGoto === 'function' && <Button circular compact size="mini" icon="time" onClick={() => onGoto(start, end)}/>}
			</Table.Cell>
			{targets
    .map(target => RotationTable.targetAccessorResolver({ start, end, targetsData, rotation }, target))
    .map((targetEntry, i) => <RotationTable.TargetCell key={`target_${i}`} {...targetEntry}/>)}
			<Table.Cell>
				<Rotation events={rotation}/>
			</Table.Cell>
			{notes
    .map(note => RotationTable.notesAccessorResolver({ start, end, targetsData, notesMap, rotation }, note))
    .map((noteEntry, i) => <Table.Cell key={`notes_${i}`} textAlign="center">
							{noteEntry}
						</Table.Cell>)}
		</Table.Row>;
